import React, { useEffect, useState } from 'react'
import eventsConst from '../eventsConst'

export default () => {
  const [allUsers, setAllUsers] = useState([])

  const [colorMap, setColorMap] = useState({})
  const [myStat, setMyStat] = useState(undefined)

  useEffect(() => {
    const iId = setInterval(() => {
      const socket = window.socket
      if (socket) {
        setMyStat(socket.id)
        clearInterval(iId)
        console.log('set my status')
      }
    }, 200)
  })

  const activeMembersHandler = (userList, colorMap) => {
    console.log('handle user list update', colorMap)
    setAllUsers(userList)
    setColorMap(colorMap)
  }

  useEffect(() => {
    window.socket.emit(eventsConst.initQuery)
    window.socket.on(eventsConst.activeMembers, activeMembersHandler)
    return () => {
      window.socket.removeListener(
        eventsConst.activeMembers,
        activeMembersHandler,
      )
    }
  }, [])

  return (
    <div>
      {myStat ? (
        <div id='my-status'>
          <i className='material-icons'>account_circle</i>
          <span
            style={{
              color: colorMap[myStat.substring(0, 5)],
            }}
          >
            {myStat.substring(0, 5)}
          </span>
        </div>
      ) : (
        'connecting...'
      )}
      <div id='user-list'>
        <div className='bold'>Active users</div>
        <ul className='clean-list'>
          {allUsers.map((u, idx) => (
            <li key={idx}>
              <div
                className='user-item'
                style={{
                  background: colorMap[u],
                }}
              >
                {u}
              </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  )
}
